* {
    box-sizing: border-box;
  }
body {
    background-color: rgb(214, 214, 214) ;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    margin: 0;
    font-family: 'Lato', sans-serif;
}
#root {
    -webkit-box-shadow: 0px 0px 16px 9px rgba(255, 255, 255, 0.6);
    -moz-box-shadow: 0px 0px 16px 9px rgba(255, 255, 255, 0.6);
    box-shadow: 0px 0px 16px 9px rgba(255, 255, 255, 0.6);

    border-radius: 25px;
}

.app {
    display: flex;
    align-items: center;
    flex-direction: column;

    padding: 20px;

    width: 500px;
    max-width: 500px;

    background-color: rgb(218, 218, 218);
    border-radius: 25px;

}

.todo-list {
    width: 100%;
}

.todo-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background: white;
    border-radius: 10px;
    margin: b;
    margin-bottom: 10px;
}

.todo {
    width: 100%;
    margin-right: 10px;
}

.comple-remove {
    display: flex;
    align-items: center;
}
.comple-remove input {
    cursor: pointer;
}
.comple-remove button:first-child {
    background-color: rgb(56, 163, 65);
    outline: none;
    border: none;
    cursor: pointer;
    border-radius: 10px;
    margin-right: 10px;
}
.comple-remove button {
    background-color: rgb(237, 46, 46);
    outline: none;
    border: none;
    cursor: pointer;
    border-radius: 10px;
    padding: 5px 8px;

    font-weight: bold;
}

.form {
    width: 100%;
    display: flex;
    justify-content: space-between;

    margin-bottom: 20px;
}
.form button {
    background: blueviolet;
    outline: none;
    border: none;
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
    padding: 10px 15px;

    border-radius: 10px;
}
.form input {
    width: 100%;
    margin-right: 10px;
    outline: none;
    text-align: center;
    border: 1px solid gold;
    padding: 10px;
    font-size: 16px;

    border-radius: 10px;
}